<template>
    <div class="wrap-goods-upload-help">
        <el-container>
            <el-header height="48" >
                   <div class="header">商品上传帮助信息</div> 
            </el-header>
            <el-container>
                <el-aside width="200px" class="aside">               
                    <el-menu class="el-menu-vertical-demo" :default-active="activeIndex">
                        <el-menu-item index="#chunk-brand">  
                            <a href="#chunk-brand" id="chunk-brand-link">品牌信息</a>
                        </el-menu-item>
                        <el-menu-item index="#chunk-freight">  
                            <a href="#chunk-freight" id="chunk-freigh">运费模板信息</a>
                        </el-menu-item>
                        <el-menu-item index="#chunk-category">  
                            <a href="#chunk-category" id="chunk-categor">类目对应属性</a>
                        </el-menu-item>
                        <el-menu-item index="#chunk-attr">  
                            <a href="#chunk-attr" id="chunk-attr-link">属性值参考</a>
                        </el-menu-item>
                        <el-menu-item index="#chunk-unit">  
                            <a href="#chunk-unit" id="chunk-unit-link">单位信息</a>
                        </el-menu-item>
                        <el-menu-item index="#chunk-color">  
                            <a href="#chunk-color" id="chunk-color-link">颜色参考</a>
                        </el-menu-item>
                    </el-menu>
            
                </el-aside>
                <el-main class="main">
                    <div class="chunk" tabindex="200" id="chunk-brand">
                        <div class="chunk-title">品牌信息</div>
                        <div class="chunk-content">
                            <el-table :data="brandsList">
                                <el-table-column type="index" width="80"></el-table-column>
                                <el-table-column label="品牌ID" prop="brandId"></el-table-column>
                                <el-table-column label="品牌名称" prop="brandName"></el-table-column>
                            </el-table>
                        </div>
                    </div>
                    <div class="chunk" tabindex="200" id="chunk-freight">
                        <div class="chunk-title">运费模板信息</div>
                        <div class="chunk-content">
                            <el-table :data="freightList">
                                <el-table-column type="index" width="80"></el-table-column>
                                <el-table-column label="运费模板ID" prop="id"></el-table-column>
                                <el-table-column label="运费模板名称" prop="templateTitle"></el-table-column>
                                <el-table-column label="仓库名称" prop="warehouseName"></el-table-column>
                            </el-table>
                        </div>
                    </div>
                    <div class="chunk" tabindex="200" id="chunk-category">
                        <div class="chunk-title">类目对应属性</div>
                        <div class="chunk-content">
                            <el-table :data="categoryToAttrList">
                                <el-table-column label="类目ID" prop="catId" width="100"></el-table-column>
                                <el-table-column label="类目名称" prop="parentCatNames"></el-table-column>
                                <el-table-column label="其他属性名称" prop="attrName" width="200"></el-table-column>
                            </el-table>
                        </div>
                    </div>
                    <div class="chunk" tabindex="200" id="chunk-attr">
                        <div class="chunk-title">属性值参考</div>
                        <div class="chunk-content">
                            <el-table :data="attrList">
                                <el-table-column type="index" width="80"></el-table-column>
                                <el-table-column label="属性名称" prop="name" width="200"></el-table-column>
                                <el-table-column label="属性值" prop="value"></el-table-column>
                                
                            </el-table>
                        </div>
                    </div>
                    <div class="chunk" tabindex="200" id="chunk-unit">
                        <div class="chunk-title">单位信息</div>
                        <div class="chunk-content">
                            <el-table :data="unitList">
                                <el-table-column type="index" width="80"></el-table-column>
                                <el-table-column label="中文单位名称" prop="cn_name" ></el-table-column>
                                <el-table-column label="英文单位名称" prop="en_name"></el-table-column>                               
                            </el-table>
                        </div>
                    </div>
                    <div class="chunk" tabindex="200" id="chunk-color">
                        <div class="chunk-title">颜色参考</div>
                        <div class="chunk-content">
                            <GoodsColors></GoodsColors>
                        </div>
                    </div>
                </el-main>
            </el-container>

        </el-container>
    </div>
</template>
<script>
import GoodsColors from './goods-color.vue';
import Attrs from '@/json/attr-value.json';
export default {

    components:{
        GoodsColors,
    },
    data(){
        return {
            brandsList:[],
            freightList:[],
            categoryToAttrList:[],
            attrList:Attrs.rows,
            activeIndex:'',
            unitList:[]
        }
    },
    methods:{
        //获取品牌
        getBrandList() {  
            this.axios.post("/proxy/fv/supplier/goods.php?act=getSupplierBrand",{body:{}}).then(res => {
                let data = res.data;
                this.brandsList = data.body.rows;
            });

        },
        //运费模板信息
        getfreightList(){
            let params={
                    pager: {
                        page: 1,
                        pageSize: 1000,
                        isSort: true,
                        order: "desc",
                        sort: "create_time"
                    },
                    
                }
            this.axios.post("/proxy/freight/api/template/list", {body:params}).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == "10000") {
                    let arr=JSON.parse(JSON.stringify(data.body.rows));
                    let tmp=[];
                    for(let i in arr){
                        if(arr[i].supplierId!='0'){
                            tmp.push(arr[i]);     
                        }
                    }
                    this.freightList=tmp;
                }
            });
        },
        //获取商品属性对应分类
        getCategoryToAttr(){
            // /api/category/attr/all
            this.axios.post("/proxy/goods/api/category/attr/all", {body:{}}).then(res => {
                let data=res.data;
                if(data.head.code='10000'){
                    this.categoryToAttrList=data.body.rows;
                }
            })
        },
        //获取单位列表
        getUnits(){        
            this.axios.post('/proxy/fv/supplier/goods.php?act=get_goods_units',{body:{}}).then(res=>{
                let data=res.data;
                if(data.head.code=='10000'){
                    this.unitList=data.body.gu_list
                }
            })
        }    
    },
    
    created(){
        this.getBrandList();
        this.getfreightList();
        this.getCategoryToAttr();
        this.getUnits();
        this.activeIndex=this.$route.hash;
    },
    mounted(){
        let $dom=document.querySelector(this.activeIndex+'-link');
        if($dom){
            $dom.click();
        }
       
        
    }
}
</script>

<style lang="less" >
    .goods-main{
        height: 100%;
    }
    .wrap-goods-upload-help{
        padding-right: 15px;
        
        height: 100%;
        .el-container{
            height: 100%;
        }
        .el-header{
            padding:0;
            border-bottom:1px solid #ccc;
            .header{
                height: 100%;
                line-height: 48px;
                padding-left:30px;
                color:#222;
                font-size: 16px;
                font-weight: bolder;
                text-align: center;
            }
        }
        .aside{
            padding-top: 20px;
            .el-menu-item.is-active{
                background:  rgba(64, 158, 255,0.2);
                a{
                    color: rgb(64, 158, 255);
                }
            }
            a{
                text-decoration: none;
                color:#222;
                display:block;
                height:100%;
            }
        }
        .main{
            padding:0;
            padding-left: 15px;
            padding-bottom: 30px;
            border-left: 1px solid #ccc;
            .chunk-title{
                font-size: 14px;
                color:#222;
                font-weight: bolder;
                margin-top:15px;
                margin-bottom: 10px;
            }
            height: 100%;
            overflow-y: auto;
        }
    }
</style>



